import { Box, Button, Grid, List, ListItem, ListItemText, Typography, useMediaQuery } from "@mui/material";
import { useCallback, useEffect } from "react";

export default function Start({setView, setWidth, setHeight, setBombs}){
  const matches = useMediaQuery("(min-width:500px)");

  const handleKeyPress = useCallback((event) => {
    if (event.key === "1") {
      handleClick(0);
    } else if (event.key === "2") {
      handleClick(1);
    } else if (event.key === "3") {
      handleClick(2);
    } else if (event.key === "4") {
      handleClick(3);
    }
  }, []);
  
  useEffect(() => {
    // 
    window.addEventListener('keydown', handleKeyPress);
    // 
    return () => {
      window.removeEventListener('keydown', handleKeyPress);
    };
  }, [handleKeyPress]);


  const handleClick = (value) => {
    switch (value){
      // 
      case 0:
        setWidth(10);
        setHeight(10);
        setBombs(15);
        setView(2);
        break;
      // 
      case 1:
        setWidth(13);
        setHeight(13);
        setBombs(30);
        setView(2);
        break;
      // 
      case 2:
        setWidth(16);
        setHeight(16);
        setBombs(50);
        setView(2);
        break;
      // 
      case 3:
        setView(1);
        break;
      default:
        break;
    }
  }

  return(
    <Box>
      {matches ?
        <Typography variant="h2" align="center" marginY={5} fontWeight={700}></Typography> :
        <Typography variant="h5" align="center" marginY={5} fontWeight={700}></Typography>
      }
      <Box flexGrow={1}>
      <Grid container spacing={1} justifyContent={"center"}>
        <Grid item >
          <Button onClick={()=>{handleClick(0)}} sx={{paddingX:3,paddingY:1,background:'linear-gradient(#f1e767 0.46%, #feb645 100.87%)',fontSize:{xs: 10, sm: 20},color:"black",border:2}}>1:</Button>
        </Grid>
        <Grid item >
          <Button onClick={()=>{handleClick(1)}} sx={{paddingX:3,paddingY:1,background:'linear-gradient(#f1e767 0.46%, #feb645 100.87%)',fontSize:{xs: 10, sm: 20},color:"black",border:2}}>2:</Button>
        </Grid>
        <Grid item >
          <Button onClick={()=>{handleClick(2)}} sx={{paddingX:3,paddingY:1,background:'linear-gradient(#f1e767 0.46%, #feb645 100.87%)',fontSize:{xs: 10, sm: 20},color:"black",border:2}}>3:</Button>
        </Grid>
        <Grid item >
          <Button onClick={()=>{handleClick(3)}} sx={{paddingX:3,paddingY:1,background:'linear-gradient(#f1e767 0.46%, #feb645 100.87%)',fontSize:{xs: 10, sm: 20},color:"black",border:2}}>4:</Button>
        </Grid>
      </Grid>
      </Box>
      <Box sx={{display:"flex", justifyContent:"center"}} marginTop={5}>
        <Box>
          <Typography variant="h6"></Typography>
          <List>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <Typography sx={{ fontSize: {xs: 10, sm: 15} }}>101015</Typography>
            </ListItem>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <Typography sx={{ fontSize: {xs: 10, sm: 15} }}>131330</Typography>
            </ListItem>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <Typography sx={{ fontSize: {xs: 10, sm: 15} }}>161650</Typography>
            </ListItem>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <Typography sx={{ fontSize: {xs: 10, sm: 15} }}></Typography>
            </ListItem>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <Typography sx={{ fontSize: {xs: 10, sm: 15} }}></Typography>
            </ListItem>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <ListItemText primary="" primaryTypographyProps={{fontSize: {xs: 10, sm: 15}}} secondary="" secondaryTypographyProps={{fontSize: {xs: 8, sm: 12}}} />
            </ListItem>
            <ListItem sx={{background:"white",borderLeft:5,borderColor:"green",marginBottom:"5px"}}>
            <ListItemText primary="" primaryTypographyProps={{fontSize: {xs: 10, sm: 15}}} secondary="f" secondaryTypographyProps={{fontSize: {xs: 8, sm: 12}}} />
            </ListItem>
          </List>
        </Box>
      </Box>
    </Box>
  );
};